import { ref } from 'vue';
import { fromCode, getdate } from '@/utils/validate';
export default {
  setup() {
    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    const XnxqOption = ref([]);
    const dialogVisible = ref(false);
    return {
      XnxqOption,
      handleClose,
      dialogVisible
    };
  },

  data() {
    return {
      // 实验室分页
      syscurrentPage: 1,
      syspagesize: 10,
      syssearch: "",
      // 搜索框
      YxsList: [],
      Yxs: {},
      // 学年学期信息
      XnxqList: [],
      Xnxq: {},
      // 实验室
      SysList: []
    };
  },

  created() {
    const user = sessionStorage.getItem("userInfo");

    if (user) {
      this.userInfo = JSON.parse(fromCode(user)); // 根据请求获取用户信息
    } else {
      this.$router.push("/login");
    }

    this.getYxsById(this.userInfo.yxsh);
    this.getXnxqList();
  },

  methods: {
    // 获取院系所信息
    getYxsById(value) {
      this.$axios.post("/weixin-yxs/yxsById", {
        id: value
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.Yxs = data.data;
        }
      });
    },

    // 分页
    syshandleSizeChange: function (size) {
      this.syspagesize = size;
    },
    syshandleCurrentChange: function (currentPage) {
      this.syscurrentPage = currentPage;
    },

    // 获取 学期学年列表
    getXnxqList() {
      this.$axios.get("/weixin-ggjxzl/getXnxqList").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.XnxqList = data.data;
        } else {
          ElMessage.error("获取学年数据错误");
        }
      });
    },

    // 获取实验室列表
    getSysListById(xnid, id) {
      this.$axios.post("/weixin-sysxk/syscountcourse", {
        xnxq: xnid,
        yxsh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.SysList = data.data;
        }
      });
    },

    // 选择学年
    changeXnxq() {
      this.Xnxq = JSON.parse(JSON.stringify(this.XnxqOption));
      this.XnxqOption = this.Xnxq.xnxqh;
      Loading.show();
      this.getSysListById(this.Xnxq.xnxqh, this.userInfo.yxsh);
      Loading.hide();
    }

  },
  computed: {
    getSysFilter() {
      return this.SysList.map(t => {
        if (t.systype == 0) {
          t['state'] = "开放";
        } else if (t.systype == 1) {
          t['state'] = "禁用";
        } else {
          t['state'] = "报修";
        }

        return t;
      }).filter(data => !this.syssearch || data.sysmc.includes(this.syssearch.toLowerCase()) || data.sysmph.includes(this.syssearch.toLowerCase()) || data.state.includes(this.syssearch.toLowerCase()));
    }

  }
};